import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { FormsModule} from '@angular/forms';

//FIREBASE
import { AngularFireModule } from 'angularfire2';
import { AngularFirestoreModule } from 'angularfire2/firestore'
import { AngularFireAuthModule } from 'angularfire2/auth'
import { AngularFireStorageModule} from 'angularfire2/storage'

import { AppComponent } from './app.component';
import { HeaderComponent } from './header/header.component';
import { SidebarComponent } from './sidebar/sidebar.component';
import { LoginComponent } from './login/login.component';
import { SignupComponent } from './signup/signup.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { HomeComponent } from './home/home.component';
import { LandingComponent } from './landing/landing.component';
import { FooterComponent } from './footer/footer.component';
import { ProfileComponent } from './profile/profile.component';
import { ApiService } from './api.service';
import {HttpModule } from '@angular/http'



//extenals
import { Ng2SearchPipeModule } from 'ng2-search-filter';
import { OrderModule } from 'ngx-order-pipe';
import { ChartsModule } from 'ng2-charts';
import { NotificationsComponent } from './notifications/notifications.component';
import { ComplainComponent } from './complain/complain.component';
import { CategoriesComponent } from './categories/categories.component';
import { OrdersComponent } from './orders/orders.component';
import { ItemsComponent } from './items/items.component';




let ROUTES =[
  {path:'', redirectTo:'login', pathMatch:'full'},
  {path:'landing', component:LandingComponent},
  {path:'signup', component:SignupComponent},
  {path:'login', component:LoginComponent},
  {path:'dashboard', component:DashboardComponent, children:[
    {path:'', redirectTo:'home', pathMatch:'full'},
    {path:'home', component:HomeComponent},
    {path:'profile', component:ProfileComponent},
    {path:'categories', component:CategoriesComponent},
    {path:'orders', component:OrdersComponent},
    {path:'items', component:ItemsComponent},
    {path:'notifications', component:NotificationsComponent},


  ]},
];




let firebaseConfig= {
  apiKey: "AIzaSyCCK8IKYcE42mFTsiVCmig8T2wxTeJfD84",
  authDomain: "paisano-pizza.firebaseapp.com",
  databaseURL: "https://paisano-pizza.firebaseio.com",
  projectId: "paisano-pizza",
  storageBucket: "paisano-pizza.appspot.com",
  messagingSenderId: "1028859967240"
};

@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    SidebarComponent,
    LoginComponent,
    SignupComponent,
    DashboardComponent,
    HomeComponent,
    CategoriesComponent,
    OrdersComponent,
    LandingComponent,
    FooterComponent,
    ProfileComponent,
    ItemsComponent,
    NotificationsComponent,
 
  ],
  imports: [
    BrowserModule,
    RouterModule.forRoot(ROUTES),
    FormsModule,
    AngularFireModule.initializeApp(firebaseConfig),
    AngularFirestoreModule,
    AngularFireAuthModule,
    Ng2SearchPipeModule,
    OrderModule,
    ChartsModule,
    AngularFireStorageModule,
    HttpModule
    
  ],
  providers: [ApiService],
  bootstrap: [AppComponent]
})
export class AppModule { }



