import { Component, OnInit } from '@angular/core';
import { ApiService } from '../api.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  constructor(private api:ApiService) { }


  items;
  workers;
  guardians;
  orders;
  categories;
  ngOnInit() {
  this.api.getItems().subscribe(resp=>{
    this.items = resp.length;
  });
  this.api.getOrders().subscribe(respx=>{
    this.orders =respx.length;
  });
  this.api.getCategories().subscribe(respxx=>{
    this.categories = respxx.length;
  });
  this.api.getWorkers().subscribe(workersx=>{
    this.workers = workersx.length;
  })
  this.api.getGuardians().subscribe(guard=>{
    this.guardians =guard.length;
  })
  
  }

}



